.overlay {
  height: 0;
  width: 100%;
  position: fixed; /* Stay in place */
  z-index: 1;
  left: 0;
  top: 0;
  background-color: rgb(0, 0, 0); /* Black fallback color */
  background-color: rgba(0, 0, 0, 0.9); /* Black w/opacity */
  overflow: hidden; /* Disable horizontal scroll */
  transition: 0.5s;
}
.modern-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  min-height: 100vh;
}
.modern-nav ul {
  position: relative;
}
.modern-nav ul li {
  text-align: center;
  list-style: none;
}
.modern-nav ul li a {
  color: #333;
  text-decoration: none;
  font-size: 3em;
  padding: 20px 20px;
  font-weight: 700;
  display: inline-flex;
  transition: 0.5s;
}

.modern-nav ul:hover li a {
  color: #0002;
}
.modern-nav ul li:hover a {
  color: #000;
  background: #fff;
}
.modern-nav ul li a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5em;
  color: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  opacity: 0;
  z-index: -1;
  transition: letter-spacing 0.5s, left 0.5s;
  font-weight: 900;
  letter-spacing: 500px;
  text-transform: uppercase;
}
