@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }
  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

.modern-nav ul li a:hover::before {
  content: attr(data-text);
  left: 50%;
  opacity: 1;
  letter-spacing: 10px;
  width: 1800px;
  height: 1800px;
}

.modern-nav ul li:nth-child(6n + 1) a::before {
  background: #81ecec;
}
.modern-nav ul li:nth-child(6n + 2) a::before {
  background: #ff7675;
}
.modern-nav ul li:nth-child(6n + 3) a::before {
  background: #ffeaa7;
}
.modern-nav ul li:nth-child(6n + 4) a::before {
  background: #a29bfe;
}
.modern-nav ul li:nth-child(6n + 5) a::before {
  background: #fd79a8;
}
